/* 0. Reset ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* 1. Main Layout ================================= */
* {
	box-sizing: border-box;
}

body {
	background: #ccc;
}

/* 2. Content ================================= */
.content {
	position: relative;
	font-family: sans-serif;
	width: 90%;
	max-width: 500px;
	margin: 140px auto;
	padding: 30px 20px 30px 30px;
}

.content .foreground {
	position: relative;
	z-index: 2;
	transform: translateZ(100px);
}

.content .background {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background: white;
	z-index: 1;
	transition: 0.2s;
	transform: translateX(20px) perspective(600px) rotateY(15deg);
}

.content:hover .background {
	transform: translateX(-30px) perspective(600px) rotateY(-15deg);
}

.content .logo {
	position: absolute;
	top: -30px;
	width: 60px;
	z-index: 2;
	transform: translateZ(100px);
	transition: 0.3s;
}

.content .logo:hover {
	transform: rotate(-0.4turn);
}

.content h1 {
	font-weight: 700;
	font-size: 22pt;
	margin: 15px 0;
}

.content p {
	color: #777;
	line-height: 140%;
}

.content p.github {
	font-size: 0.8em;
}

.content p.github a {
	opacity: 0.2;
	transition: 0.3s;
}

.content p.github a:hover {
	opacity: 1;
	color: red;
}

.content code {
	position: absolute;
	top: 20px;
	right: 20px;
	font-size: 0.8em;
	opacity: 0.2;
}

.secret {
	position: absolute;
	visibility: hidden;
	height: 100vh;
	width: 100vw;
	background: yellow;
	z-index: 3;
	top: 0px;
	font-family: 'Modak', cursive;
	letter-spacing: .4rem;
	font-size: 5rem;
	line-height: 3;
	text-align: center;
}

.secretbox {
	background: pink;
	padding: 20px;
	width: 50%;
	position: absolute;
	top: 103px;
	align-items: center;
	z-index: 2;
	margin-left: 25%;
	box-shadow: 5px 10px 8px 10px #888888;
	visibility: hidden;
}

.secretbox h1 {
	font-family: 'Modak', cursive;
	letter-spacing: .4rem;
	font-size: 2rem;
	line-height: 1.5;
	text-align: center;
}

.secretbox span {
	border-bottom: 1px solid blue;
	font-style: italic;
	font-weight: bolder;
	letter-spacing: .2rem;
	color: blue;
}

.secretbox .link {
	color: red;
	border-bottom: none;
	margin-left: 6px;
	font-size: .7rem;
	letter-spacing: 0.6px;
}

.secretbox p {
	font-family: 'Poppins', sans-serif;
	line-height: 2;
	color: black;
	text-align: center;
}
